___CSS_LOADER_EXPORT___.push([module.id, `* {
	vertical-align: baseline;
	font-weight: inherit;
	font-family: inherit;
	font-style: inherit;
	font-size: 100%;
	border: 0 none;
	outline: 0;
	padding: 0;
	margin: 0;
}

a {
    color: black;
    text-decoration: none;
}

body {
    height: 100vh;
    width: 100vw;
}

.header-container {
    background-color: rgba(0, 0, 255, .9);
    height: 15%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

#header-text {
    font-size: 3rem;
}

.main-container {
    height: 75%;
    width: 100%;
    background-color: rgba(0, 0, 0, .8);
    display: grid;
    grid-template-columns: 5% 95%;
    grid-template-rows: 10% 10% 5% 70% 5%;
}

.turn-announcement-container {
    grid-area: 1 / 1 / 2 / 3;
    text-align: center;
    font-weight: bold;
}

.turn-announcement-container h2 {
    font-size: 2rem;
}

.board-info-container {
    display: flex;
    grid-area: 2 / 1 / 3 / 3;
    align-items: end;
    justify-content: center;
    font-weight: bold;
}

.grid-container {
    display: flex;
    flex-direction: column;
    height: 80%;
    width: 100%;
    align-items: center;
    justify-content: center;
    grid-area: 4 / 2 / 5 / 3;
}

.column-labels-container {
    font-weight: bold;
    display: grid;
    grid-template-columns: repeat(10, 2rem);
    grid-area: 3 / 2 / 4 / 3;
    align-items: end;
    justify-content: center;
    text-align: center;
    gap: .1rem;
}

.row-labels-container {
    font-weight: bold;
    display: grid;
    grid-area: 4 / 1 / 5 / 2;
    align-items: center;
    justify-content: end;
    text-align: center;
    grid-template-rows: repeat(10, 2rem);
    gap: .15rem;
}

.grid {
    display: grid;
    height: min-content;
    width: min-content;
    grid-template-columns: repeat(10, 1fr);
    grid-template-rows: auto;
    gap: 0;
    align-items: center;
    justify-content: center;
}

.cell {
    height: 2rem;
    width: 2rem;
    border: black solid 1px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.cell:hover {
    border: darkblue solid 1px;
}

.error-message {
    grid-area: 5 / 1 / 6 / 3;
    color: red;
}

.footer-container {
    background-color: rgba(0, 0, 255, .9);
    height: 10%;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    padding-right: .5%;
}

.credit-container {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: flex-end;
    padding-bottom: .3%;
}

.has-ship {
    background-color: blue;
}

.is-hit {
    height: 1rem;
    width: 1rem;
    background-color: red;
    border-radius: 50%;
    display: inline-block;
}

.hidden {
    display: none;
